body {
  background-color: #fafafa;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.main {
  display: flex;
  flex-direction: column;
}

.navigation {
  z-index: 999;
  height: 8vh;
}
.pasfoto {
  border-radius: 30px;
  width: 25rem;
  height: 30rem;
  object-fit: cover;
}
.carousel-inner {
  display: flex;
}

.row {
  height: auto !important;
}

.btn {
  height: 20px;
}

.carousel-item {
  background-color: white;
}

.span {
  color: white;
}

Nav.Link:active{
  background-color: grey;
}

.leerdoel-navigation {
  z-index: 999;
  position: fixed !important;
}

@media  only screen and (max-device-width: 1200px) {
  .leerdoel-navigation {
    position: relative !important;
  }
}
.project {
    background-color: #fff;
}

.start {
    color: #fff;
    background-color: aquamarine;
}

.vertical-timeline-element-icon {
    box-shadow: 0 0 0 2px black, inset 0 2px 0 rgb(0 0 0 / 8%), 0 3px 0 4px rgb(0 0 0 / 5%) !important;
}

table {
    border: 2px solid black;
    width: 100%;
    height: auto;
}

th {
    border-right: 1px solid grey;
    border-bottom: 1px solid black;
    text-align: center;
}

td {
    padding: 5px;
    text-align: left;
    border-right: 1px solid grey;
    border-bottom: 1px solid grey;
}
.accordion-button:not(.collapsed) {
    color: black;
    background-color: #f8f9fa !important;
}

.accordion-button:focus {
    border-color:  #f8f9fa !important;
    box-shadow: 0 0 0 0.25rem lightgrey;
}
.voorraad {
    width: 100%;
}
.standard {
    width: 50%;
}

.done {
    width: 100%;
}

.feedback {
    width: 100%;
    height: auto;
}
.jira {
    width: 100%
}
.btn {
    height: auto;
}

.nav-link {
    color: #343a40 !important;
}

.nav-link.active {
    color: white !important;
    background-color: #343a40 !important;
}

.leerdoelGeneral {
    padding: 2rem;
}

.leerdoel {
    padding: 1rem;
    font-family: monospace, monospace;
    background-color: #f8f9fa;
    border-radius: 8px;
}

.offcanvas {
    height: 100%;
}

.canvasContainer {
    color: black;
}

.containerStyle{
    border-bottom: double grey;
    border-bottom-width: 10px;
}
.images {
    width: 100%;
}
.first {
    width: 90%;
    height: 25rem;
    object-fit: contain;
}

.second {
    width: 90%;
    height: 15rem;
    object-fit: contain;
}

.index {
    width: 90%;
    height: 20rem;
    object-fit: contain;
}

.styling {
    width: 90%;
    height: 25rem;
    object-fit: contain;
}

.team {
    width: 90%;
    height: 30rem;
    object-fit: contain;
}
.pdf {
    width: 90vw;
    height: 90vh;
}
.pdf {
    width: 90vw;
    height: 90vh;
}
.quote {
    background-color: #f8f9fa;
    border-left-style: solid !important;
    border-left: black;

    padding: 2rem
}

.highlight {
    font-weight: bold;
}
